import React,{useState,useEffect} from 'react'
import Producto from '../Components/Producto'
import { Spinner, CardColumns } from 'react-bootstrap'

import { useMatomo } from '@datapunt/matomo-tracker-react'

function HomePage(){
    const[cargando,setCargando] = useState(true)
    const[productos,setProductos] = useState([])

    const { trackPageView, trackEvent } = useMatomo()

    // Track page view
    React.useEffect(() => {
        trackPageView({
            documentTitle: 'home', // optional
            href: 'http://frontend.local/', // optional
            customDimensions: [
                {
                    id: 1,
                    value: 'home',
                },
            ], // optional
        })
    }, [])

    useEffect(
        ()=>{
            fetch("https://jsonfy.com/items")
            .then(res=>res.json())
            .then(data=>{
                setCargando(false)
                setProductos(data)
            })
        },
        []
    )

    if(cargando){
        return(
            <>
                <Spinner animation="border" role="status"></Spinner>
                <span className="sr-only">Cargando...</span>
            </>
        )
    }else{
        return(
            <CardColumns>
                {productos.map(producto => <Producto datos={producto} verDetalle={true} sizeImg={{ width: '18rem' }}/>)}
            </CardColumns>
        )
    }
}

export default HomePage;