import React, { useState } from 'react'
import FormGroup from '../Components/Forms/FormGroup';
import { Button } from 'react-bootstrap'
import { useMatomo } from '@datapunt/matomo-tracker-react'

function RegistroPage(){
    const [form, setForm] = useState({ nombre: '', apellido: '', email: '', password: '' })
    const [mensaje, setMensaje] = useState("")

    const { trackPageView, trackEvent } = useMatomo()

    // Track page view
    React.useEffect(() => {
        trackPageView({
            documentTitle: 'registro', // optional
            href: 'http://frontend.local/registro', // optional
            customDimensions: [
                {
                    id: 3,
                    value: 'Registro',
                },
            ], // optional
        })
    }, [])

    function handleChange(e) {
        const target = e.target;
        const value = target.value;
        const name = target.name;

        setForm({
            ...form, [name]: value
        })
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setMensaje('Registro Correcto!!')
        trackEvent({ category: 'Registro', action: 'click-event-registro' })
    }

    return(
        <div>
            <h1>Registro de Usuario</h1>
            <form onSubmit={handleSubmit}>
                <FormGroup label="Nombre" name="name" type="text" placeholder="Ingrese el nombre" value={form.name} change={handleChange} />
                <FormGroup label="Apellido" name="apellido" type="text" placeholder="Ingrese el apellido" value={form.apellido} change={handleChange} />
                <FormGroup label="Email" name="email" type="text" placeholder="Ingrese el email" value={form.email} change={handleChange} />
                <FormGroup label="ContraseÃ±a" name="password" type="text" placeholder="Ingrese el password" value={form.password} change={handleChange} />
                <Button type="submit">Registrarse</Button>                
            </form>
            <div><h2>{mensaje}</h2></div>
        </div>         
    )

}

export default RegistroPage;