import React, {  useState } from 'react'
import FormGroup from '../Components/Forms/FormGroup'
import { Button } from 'react-bootstrap'

import { useMatomo } from '@datapunt/matomo-tracker-react'


function LoginPage(){
    const [form, setForm] = useState({ email: '', password: '' })
    const [mensaje, setMensaje] = useState("")

    const { trackPageView, trackEvent } = useMatomo()

    // Track page view
    React.useEffect(() => {
        trackPageView({
            documentTitle: 'login', // optional
            href: 'http://frontend.local/login', // optional
            customDimensions: [
                {
                    id: 2,
                    value: 'Login',
                },
            ], // optional
        })
    }, [])

    function handleChange(e) {
        const name = e.target.name
        const value = e.target.value

        setForm({
            ...form, [name]: value
        })
    }

    const handleSubmit = async (e) => {
        e.preventDefault();        
        setMensaje('Login Correcto!')
        trackEvent({ category: 'Login', action: 'click-event-login' })

    }

    return(
        <div>
            <div><h2>{mensaje}</h2></div>
            <form onSubmit={handleSubmit}>
                <FormGroup label="Email" name="email" type="text" placeholder="Ingrese el email" value={form.email} change={handleChange} />
                <FormGroup label="Password" name="password" type="password" placeholder="Ingrese el password" value={form.password} change={handleChange} />
                <Button type="submit">Login</Button>
            </form>
        </div>
    )
}

export default LoginPage;