import React, { useState} from 'react'
import {Link} from 'react-router-dom'
import { Card, Button } from 'react-bootstrap'
import { useMatomo } from '@datapunt/matomo-tracker-react'

function Producto(props){        
    const { trackPageView, trackEvent } = useMatomo()
    const [mensaje,setMensaje] = useState("")
    //const { datos } = props    
    //const{id,name,description,price,photo_url,stock} = datos

    //console.log("id",id)

    const verDetalle = (props.verDetalle!==false?true:false)

    function handleOnClickCompra(){        
        setMensaje('Gracias por su compra!!!')
        trackEvent({ category: 'DetallePage', action: 'click-event-comprar' })
        
        
    }

    return(           
        <Card style={props.sizeImg}>
            <Card.Img variant="top" src={props.datos.photo_url}/>
            <Card.Body>
                <Card.Title>{props.datos.name}</Card.Title>
                {
                verDetalle ?
                <></>
                :
                <Card.Text>
                    {props.datos.description}
                </Card.Text>
                }
                {
                    verDetalle ?
                    <Link to={"/detalle/" + props.datos.id}>
                        <Button variant="primary">
                            Ver Detalle
                        </Button>
                    </Link>
                    :                 
                        <>                                              
                        <Button variant="primary" onClick={handleOnClickCompra}>
                            Comprar
                        </Button>
                            <div><h2>{mensaje}</h2></div>
                        </>
                    
                }               
            </Card.Body>
            <Card.Footer>
                <small className="text-muted">Precio ${props.datos.price} Stock: {props.datos.stock}</small>
            </Card.Footer>            
        </Card>      
        
    )
}

export default Producto;